export default {
  data () {
    return {
      download: {
        list: []
      }
    }
  },
  created () {
    // 装入音乐控件
    this.Music = this.$store.state.Music
    this.download = this.Music.download
  },
  methods: {

    /**
     * 全部音乐开始下载
     */
    allStart () {
      this.Music.downloadState = !0
      this.Music.allDownloadStart()
    },

    /**
     * 全部停止
     */
    allStop () {
      this.Music.downloadState = !1
    }
  }
}
